@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap');
body {
  font-family: "Open Sans", sans-serif;
  background:#f7f7f7;
}
h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif;
}
/* loader style */
.loader.loader-fade-up 
{
  top: -120%;
}
.loader
{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index:1000;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: .3s ease-in-out;
}
/* loader style */
/* header-hero style*/
.header-hero
{
  position: relative;
  width: 100%;
  background: url("assets/images/home-bg.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
/* header-hero style*/

/* header style */
header ul li:hover
{
  color: red;
} 
header ul li.active 
{
  color: red;
} 
header ul li 
{
  margin: 10px;
  font-size: 16px;
  cursor: pointer;
  color: rgb(95, 95, 95);
  transition: .3s ease-in-out;
  transform: translateY(3px);
}
header ul::before
{
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  height: 200px;
  width: 100%;
  top: -100%;
  background: #fff;
  z-index: 1;
  display: none;
} 
header ul 
{
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: row;
  list-style: none;
  transition: .3s ease-in-out;
}
header .logo i 
{
  color: red;
  margin-right: 10px;
}
header .logo 
{
  font-size:35px;
  text-transform: capitalize;
  color: rgb(95, 95, 95);
  cursor: pointer;
  z-index: 2;
}
header div:last-child i 
{
  position: relative;
  font-size: 28px;
  cursor: pointer;
  color: #000;
}
header div:last-child 
{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform: translateY(12px);
}
header div:first-child
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  transform: translateY(12px);
}
header 
{
  position:relative;
  width: 100%;
  z-index: 2;
}
header.active-header 
{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  background: #fff;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.179);
}
@media screen and (min-width:787px){
  header div:first-child 
  {
    transform: translateY(3px);
  }
  header div:last-child 
  {
    display: none;
  }
}
@media screen and (max-width:786px){
  header ul::before
  {
    display: block;
  }
  header ul 
  {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    visibility: hidden;
    opacity: 0;
    height:10px;
  }
  header div:last-child
  {
    padding-right: 20px;
  }
  header div:first-child
  {
    position: relative;
    padding-left: 20px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
  }
  header
  {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
}

header.navbar-mobile ul{
  visibility: visible;
  opacity: 1;
  height: 264px;
}
header.navbar-mobile
{
  border-bottom: 1px solid rgba(0, 0, 0, 0.172);
}
/* header style */

/* hero style */
.hero .col-lg-6:first-child
{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.hero .row .order-now:hover::before
{
  width: 100%;
  left: 0;
  z-index:-1;
}
.hero .row .order-now:hover
{
  color: #fff;
}
.hero .row .order-now::before
{
  content: '';
  position: absolute;
  right: 0;
  height: 100%;
  width: 0;
  top: 0;
  transition: .4s linear;
  background: red;
}
.hero .row .order-now
{
  position: relative;
  text-transform: capitalize;
  border: 2px solid red;
  padding: 8px 32px;
  border-radius: 10px;
  text-decoration: none;
  cursor: pointer;
  color: red;
  overflow: hidden;
  z-index: 0;
  transition: .4s linear;
}
.hero .row h3 
{
  font-size: 40px;
  text-transform: capitalize;
}
.hero .hero-img 
{
  animation: mohamed 3s linear infinite;
}
@keyframes mohamed {
  0%, 100%{
    transform: translateY(0rem);
  }
  50%{
    transform: translateY(3rem);
  }
}
.hero .row 
{
  padding-top: 50px;
}
.hero 
{
  position: relative;
  width: 100%;
  height: 600px;
}
@media screen and (max-width:991px){
  .hero 
  {
    height: auto;
    padding-bottom: 50px;
  }
}
/* hero style */

/* dishes style */
.section-tittle span 
{
  color: red;
}
.section-tittle 
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  color: rgb(100, 100, 100);
  margin-bottom: 50px;
}
.dishes .dish-box .data-img 
{
  position: absolute;
  width: 100%;
  height: 100%;
  top:-100%;
  object-fit: cover;
  margin: 0 auto;
  transition: .3s ease-in-out;
} 
.dishes .dish-box h4 
{
  color: #000;
  font-weight: 600;
  text-transform: capitalize;
}
.dishes .dish-box p 
{
  font-size: 14px;
  text-transform: capitalize;
  width: 90%;
  margin: 0 auto;
}
.dishes .dish-box:hover .content 
{
  transform: translateY(100%);
}
.dishes .dish-box:hover .data-img
{
  visibility: visible;
  opacity: 1;
  height: 100%;
  top: 0;
} 
.dishes .dish-box .content 
{
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  row-gap: 12px;
  padding-top: 20px;
  padding-bottom: 20px;
  transition: .3s ease-in-out;
}
.dishes .dish-box 
{
  flex:1 1 30rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
  border:.1rem solid rgba(0,0,0,.3);
  cursor: pointer;
  border-radius: .2rem;
  background: #fff;
}
.dishes 
{
  position: relative;
  width: 100%;
  padding-bottom: 50px;
  padding-top: 50px;
}
/* dishes style */

/* popular box food style*/
.popular-food-box .price 
{
  background: red;
  color: #fff;
  position: absolute;
  top: 8%;
  left:5%;
  padding: 6px 20px;
  border-radius: 6px;
  margin-left: 10px;
}
.popular-food-box h4
{
  color: #000;
  font-weight: 600;
  text-transform: capitalize;
}
.popular-food-box .container .stars 
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-bottom: 5px;
}
.popular-food-box i 
{
  color: rgb(242, 242, 1);
  margin: 3px;
}
.popular-food-box .container a:hover::before
{
  width: 100%;
  left: 0;
  z-index:-1;
}
.popular-food-box .container a:hover
{
  color: #fff;
} 
.popular-food-box .container a::before
{
  content: '';
  position: absolute;
  right: 0;
  height: 100%;
  width: 0;
  top: 0;
  transition: .4s linear;
  background: red;
} 
.popular-food-box .container a 
{
  position: relative;
  text-transform: capitalize;
  border: 2px solid red;
  padding: 8px 26px;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
  color: red;
  overflow: hidden;
  z-index: 0;
  transition: .4s linear;
}
.popular-food-box .container 
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 5px;
}
.popular-food-box 
{
  position: relative;
  background: #fff;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
  border:.1rem solid rgba(0,0,0,.3);
  padding-top: 20px;
  padding-bottom: 20px;
}
.popular 
{
  position: relative;
  width: 100%;
  padding-bottom: 50px;
}
/* popular box food style*/

/* works style */
.works .work-box h3 
{
  font-size: 25px;
  text-align: center;
  color: rgb(95, 95, 95);
  width: 80%;
  text-transform: capitalize;
  font-weight: 600;
  margin-top: 10px;
}
.works .work-box img 
{
  border: 10px solid #fff;
  border-radius: 50%;
}
.works .work-box 
{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.works 
{
  position: relative;
  padding-bottom: 50px;
}
/* works style */

/* gallery style */
.gallery .gallery-box .box-data h3 
{
  color: #000;
  font-weight: 600;
  text-transform: capitalize;
}
.gallery .gallery-box .box-data p 
{
  color: rgb(95, 95, 95);
  text-align: center;
  width: 90%;
}
.gallery .gallery-box .box-data a:hover
{
  color: #fff;
}
.gallery .gallery-box .box-data a:hover::before
{
  width: 100%;
  left: 0;
  z-index:-1;
}
.gallery .gallery-box .box-data a::before
{
  content: '';
  position: absolute;
  right: 0;
  height: 100%;
  width: 0;
  top: 0;
  transition: .4s linear;
  background: red;
} 
.gallery .gallery-box .box-data a 
{
  position: relative;
  text-transform: capitalize;
  border: 2px solid red;
  padding: 8px 26px;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
  color: red;
  overflow: hidden;
  z-index: 0;
  transition: .4s linear;
}
.gallery .gallery-box .box-data 
{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  top: -100%;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.857);
  object-fit: cover;
  transition: .3s ease-in-out;
}
.gallery .gallery-box:hover .box-data
{
  top: 0;
} 
.gallery .gallery-box 
{
  position: relative;
  border: 6px solid #fff;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
  overflow: hidden;
  cursor: pointer;
}
.gallery
{
  position: relative;
  padding-bottom: 50px;
}
/* gallery style */

/* review style */
.review-box  .stars i 
{
  margin: 3px;
  color: rgb(255, 45, 45);
  font-size: 19px;
}
.review-box  .stars
{
  position: relative;
  transform: translateY(0px);
}
.review-box  h3 
{
  transform: translateY(-10px);
}
.review-box  p 
{
  text-align: center;
  color: #f7f7f7;
  font-size: 14px;
  width: 90%;
  transform: translateY(30px);
}
.review-box  img 
{
  position: absolute;
  border: 10px solid #fff;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  top: -30%;
  transform: scale(.6);

}
.review-box  
{
  position: relative;
  background: rgb(51, 51, 51);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 360px;
  border: 8px solid #fff;
  box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.371);
}
@media screen and (max-width:991px){
  .review .col-lg-4:nth-child(3) 
  {
    margin-top: 70px;
  }
}
@media screen and (max-width:767px){
  .review .col-lg-4:nth-child(2) 
  {
    margin-top: 70px;
  }
}
.review
{
  
  padding-bottom: 50px;
}
/* review style */


/* order style */
.order form button:hover
{
  background-color: rgb(255, 94, 94);
}
.order form button
{
  position: relative;
  text-transform: capitalize;
  padding: 8px 32px;
  border-radius: 10px;
  outline:0;
  border: 0;
  color: #fff;
  background: red;
  transition: .4s ease-in-out;
}
.order form div input 
{
  position: relative;
  width: 49%;
}
.order form div 
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.order form 
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 20px;
}
.order .col-lg-6:last-child
{
  display: flex;
  justify-content: center;
  align-items: center;
}
.order 
{
  padding-bottom: 50px;
}
/* order style */


/* footer style */
p {
  color: #b8b8b8;
  font-size: 13px;
  letter-spacing:1px;
  line-height: 20px;
}
footer .col-lg-4 h1 
{
  color: #fff;
  letter-spacing: 5px;
  font-size: 35px;
}
footer .col-lg-2 h4, 
footer .col-lg-3 h4 
{
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 1px;
  color: #fff;
}
footer .col-lg-2 ul li:hover
{
  color: red;
} 
footer .col-lg-2 ul li 
{
  color: #b8b8b8;
  transition: .3s ease-in-out;
  cursor: pointer;
  font-size: 14px;
  transform: translateX(-30px);
}
footer .col-lg-2 ul
{
  list-style: none;
  display: flex;
  margin-top: 25px;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  row-gap: 10px;
}
footer .col-lg-3:nth-child(3) p 
{
  line-height:5px;
  letter-spacing: 0px;
  transform: translateY(25px);
}
footer .col-lg-3 p 
{
  color: #b8b8b8;
}
footer .col-lg-3:nth-child(3) p 
{
  font-size: 14px;
}
footer .col-lg-3:last-child p
{
  margin-top: 15px;
}
footer .col-lg-3:last-child p
{
  font-size: 13px;
  /* line-height: 25px; */
}
footer form 
{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background: rgb(228, 35, 35);
}
footer
{
  position: relative;
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
  background: rgb(44, 44, 44);
}
/* footer style */

